import React from 'react';
import {Container} from "react-bootstrap";
import MyNavbar from "../components/MyNavbar";
import AboutProject from "../components/AboutProject";
import MyFooter from "../components/MyFooter";

const Home = () => {
    return (
        <Container>
            <MyNavbar/>
            <AboutProject/>
            <MyFooter/>
        </Container>
    );
};

export default Home;