import React, { useEffect, useState } from 'react';
import MyNavbar from "../components/MyNavbar";
import { Card, Container, Spinner, Button } from "react-bootstrap";
import { TEXTS } from "../js/text";
import { getTests } from "../js/utils";

const Tests = () => {
    const [tests, setTests] = useState([]);
    const [isLoading, setIsLoading] = useState(false); //    
    const [error, setError] = useState(null); //   

    const fetchData = async () => {
        setIsLoading(true);
        setError(null);
        try {
            const response = await getTests();
            setTests(response);
        } catch (err) {
            setError(err.message || TEXTS.ERROR_COLLECTION_NOT_FOUND);
        } finally {
            setIsLoading(false);
        }
    };

    useEffect(() => {
        fetchData();
    }, []);

    return (
        <Container className="mt-3 text-center d-flex align-items-center justify-content-center">
            <MyNavbar />
            <Card className="shadow-lg mt-5" border="secondary">
                <Card.Body>
                    <Card.Title>
                        <h1>{TEXTS.TESTS}</h1>
                    </Card.Title>

                    {isLoading ? (
                        <div className="my-3">
                            <Spinner animation="border" variant="primary" />
                        </div>
                    ) : error ? (
                        <div>
                            <p className="text-danger">{error}</p>
                            <Button variant="primary" onClick={fetchData}>
                                {TEXTS.RETRY}
                            </Button>
                        </div>
                    ) : (
                        <div>
                            {tests.length > 0 ? (
                                <ul>
                                    {tests.map((test) => (
                                        <li key={test.id}>{test.id}</li>
                                    ))}
                                </ul>
                            ) : (
                                <p>{TEXTS.NO_TESTS_FOUND}</p>
                            )}
                        </div>
                    )}
                </Card.Body>
            </Card>
        </Container>
    );
};

export default Tests;
