import {hashSHA256, X} from "./hashSHA256";
import { db } from "./firebase";
import {doc, deleteDoc, collection, getDocs, addDoc, updateDoc} from "firebase/firestore";
import {TEXTS} from "./text";

export const checkHash = async t => {
    const V = await hashSHA256(t);
    return V === X;
};

export const addTest = async testData => {
    try {
        const orgCollection = collection(db, "testsList");
        const docRef = await addDoc(orgCollection, testData);
        console.log(` , ID: ${docRef.id}`);
        return docRef.id; //  ID  
    } catch (error) {
        console.error("   : ", error);
        throw error; //   
    }
};

export const removeTest = async tId => {
    try {
        const orgDoc = doc(db, "testsList", tId);
        await deleteDoc(orgDoc);
        console.log(`ID ${tId} `);
    } catch (error) {
        console.error("   : ", error);
        throw error;
    }
};

export const editTest = async (tId, updatedData) => {
    try {
        const orgDoc = doc(db, "testsList", tId);
        await updateDoc(orgDoc, updatedData);
        console.log(`ID ${tId} .`);
    } catch (error) {
        console.error("   : ", error);
        throw error;
    }
};

export const getTests = async () => {
    try {
        //     
        const orgCollection = collection(db, "testsList");
        const snapshot = await getDocs(orgCollection);

        if (!snapshot.empty) {
            const testsList = snapshot.docs.map((doc) => ({
                id: doc.id,
                ...doc.data(),
            }));
            console.log(" : ", testsList);
            return testsList;
        } else {
            throw new Error(TEXTS.ERROR_COLLECTION_NOT_FOUND);
        }
    } catch (error) {
        throw new Error(error.message || "    .");
    }
};